import React, { Fragment, memo, useEffect, useRef, useState } from "react";
import NumberFormat from "react-number-format";
import _ from "lodash";

const ComponentDetailElementList = (props) => {
  return <Wrapper2 index={props.index} {...props}></Wrapper2>;
};

const Wrapper1 = (props) => {
  return (
    <td
      className="dfkosdkjspdjsksadsdsasr"
      style={{ display: "flex", alignItems: "center", width: "40%" }}
    >
      <Rank {...props} />
      <Logo {...props} />
      <Name {...props} />
    </td>
  );
};

const Rank= (props)=> {
  return (
    <div style={{color: "#fff", width: 20, fontSize: 13, fontWeight: 600}}>{props.rank}</div>
  )
}

const Logo = (props) => {
  return (
    <span
      className="fjdaklisfjlkassdasajiorejas"
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        width: 24,
        height: 24,
        marginRight: 8,
      }}
    >
      <img
        src={
          "https://quantifycrypto.s3-us-west-2.amazonaws.com/pictures/crypto-img/32/icon/" +
          props?.qc_key?.toLowerCase().trim() +
          ".png"
        }
        alt="open"
        style={{
          width: "100%",
          height: "100%",
          objectFit: "cover",
          borderRadius: "50%",
        }}
      />
    </span>
  );
};

const Name = memo((props) => {
  return (
    <span
      title={props.symbol_name}
      className="jvkopsjfrkesasasarswoeeqa0"
      style={{
        justifyContent: "center",
        alignItems: "center",
        fontSize: 14,
        fontWeight: 600,
        marginRight: 8,
      }}
    >
      {props.symbol_name}
    </span>
  );
});

const Support = memo((props) => {
  // eslint-disable-next-line
  const [support1h, setSupport1h] = useState(() => props.support1h);
  const ref= useRef()
  useEffect(() => {
    if (
      props?.qc_key?.trim()?.toLowerCase() ===
      props?.indicator?.qc_key?.trim()?.toLowerCase() && props.indicator.key=== "support1h"
    ) {
      setSupport1h(() => props.indicator.value);
      ref.current.style.background = "rgba(22, 160, 133, 0.5)";
    } else {
      ref.current.style.background = "transparent";
    }
  }, [props]);
  return (
    <td
      className={`assajkshaksasasa kjdawoljskalssa`}
      style={{
        justifyContent: "flex-end",
        alignItems: "center",
        fontSize: 14,
        fontWeight: 600,
        position: "relative",
        height: "100%",
        display: "flex",
        color: "rgb(255, 152, 0)",
        width: "12%",
        maxWidth: "12%",
        whiteSpace: "nowrap",
      }}
    >
      $ {support1h.toFixed(2)}
      <p
        ref={ref}
        style={{
          position: "absolute",
          top: 0,
          left: 0,
          width: "100%",
          height: "100%",
          transition: "all .2s linear",
        }}
      ></p>
    </td>
  );
});

const Resistance = (props) => {
  // eslint-disable-next-line
  const [resistance1h, setResistance1h] = useState(() => props.resistance1h);
  const ref= useRef()
  useEffect(() => {
    if (
      props?.qc_key?.trim()?.toLowerCase() ===
      props?.indicator?.qc_key?.trim()?.toLowerCase() && props.indicator.key=== "resistance1h"
    ) {
      setResistance1h(() => props.indicator.value);
      ref.current.style.background = "rgba(22, 160, 133, 0.5)";
    } else {
      ref.current.style.background = "transparent";
    }
  }, [props]);
  return (
    <td
      className={`assajkshaksasasa kjdawoljskalssa`}
      style={{
        justifyContent: "flex-end",
        alignItems: "center",
        fontSize: 14,
        fontWeight: 600,
        width: "12%",
        maxWidth: "12%",
        display: "flex",
        position: "relative",
      }}
    >
      ${resistance1h.toFixed(2)}
      <p
        ref={ref}
        style={{
          position: "absolute",
          top: 0,
          left: 0,
          width: "100%",
          height: "100%",
          transition: "all .2s linear",
        }}
      ></p>
    </td>
  );
};

const Wrapper2 = (props) => {
  return (
    <Fragment>
      <Wrapper1 {...props}></Wrapper1>
      <SymbolName {...props} />
      <Price index={props.index} {...props}></Price>
      <DP {...props} />
      <Support {...props}></Support>
      <Resistance {...props}></Resistance>
    </Fragment>
  );
};

const Price = (props) => {
  const ref = useRef();
  const [price, setPrice] = useState(() => props.price_usd);
  useEffect(() => {
    if (
      props?.qc_key?.trim()?.toLowerCase() ===
      props?.changePrice?.qc_key?.trim()?.toLowerCase()
    ) {
      setPrice(() => props.changePrice.price_usd);
      ref.current.style.background = "rgba(22, 160, 133, 0.5)";
    } else {
      ref.current.style.background = "transparent";
    }
  }, [props]);

  return (
    <td
      className="fksledjkijfsaaaedikdsaassdwqa"
      style={{
        display: "flex",
        justifyContent: "end",
        alignItems: "center",
        fontWeight: 600,
        position: "relative",
        height: "100%",
        width: "12%",
        maxWidth: "12%",
      }}
    >
      <NumberFormat
        prefix="$ "
        renderText={(value, props) => (
          <div
            style={{
              fontSize: 14,
              whiteSpace: "nowrap",
              height: "100%",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              position: "relative",
            }}
          >
            {value}
            <p
              ref={ref}
              style={{
                position: "absolute",
                top: 0,
                left: 0,
                width: "100%",
                height: "100%",
                transition: "all .2s linear",
              }}
            >      
            </p>
          </div>
        )}
        decimalSeparator="."
        displayType={"text"}
        thousandSeparator={true}
        value={price.toFixed(2)}
      ></NumberFormat>
    </td>
  );
};

const SymbolName = memo((props) => {
  return (
    <td
      className={`assajkshaksasasa`}
      style={{
        justifyContent: "center",
        alignItems: "center",
        fontSize: 14,
        fontWeight: 600,
        width: "12%",
        maxWidth: "12%",
      }}
    >
      {props.qc_key}
    </td>
  );
});

const DP = memo((props) => {
  // eslint-disable-next-line
  const [price24h, setPrice24h] = useState(() => props.price24h);
  return (
    <td
      className={`assajkshaksasasan ${
        parseFloat(price24h) <= 0 ? "dawyuawhauksgfsere" : ""
      } ${parseFloat(price24h) > 0 ? "dhgsjdhgajakssaas" : ""}`}
      style={{
        justifyContent: "center",
        alignItems: "center",
        fontSize: 14,
        fontWeight: 600,
        width: "12%",
        maxWidth: "12%",
        display: "flex"
      }}
    >
      {_.round(parseFloat(price24h) * 100, 3)}%
    </td>
  );
});

export default ComponentDetailElementList;
